
































































































import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			itemList: [],
			realPrice: 999,
			orderId: 0,
			remark: '',
			payWay: 'wxpay'
		}
	},
	computed: mapState(['user','addressInfo']),
	onLoad: function(parameter) {
		this.orderId = parameter.order_id;
		this.realPrice = parameter.real_price;
		uni.showLoading({
			title: '加载中'
		});
		uni.request({
			url: this.GLOBAL.serverSrc + 'pay/create_order/select',
			method: 'POST',
			data: {
				order_id: this.orderId
			},
			success: res => {
				uni.hideLoading();
				if (res.data.status === 200) {
					this.itemList = res.data.itemList;
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					});
				}
			},
			fail: (e) => {
				uni.hideLoading();
				this.GLOBAL.requestFail(e);
			}
		});
	},
	methods: {
		changeWxpay() {
			this.payWay = 'wxpay';
		},
		changeAlipay() {
			this.payWay = 'alipay';
		},
		goPay() {
			if (this.payWay === 'wxpay') {
				this.wxpay();
			} else if (this.payWay === 'alipay') {
				this.alipay();
			} else {
				uni.showToast({
					title: '请选择一个付款方式哦！',
					icon: 'none'
				})
			}
		},
		wxpay() {
			uni.request({
				url: this.GLOBAL.serverSrc + 'pay/wepay/payorder',
				method: 'POST',
				data: {
					order_id: this.orderId,
					remark: this.remark
				},
				success: res => {
					var wxpaySdk = res.data.wepay_sdk;
					uni.requestPayment({
						provider: 'wxpay',
						orderInfo: wxpaySdk, //微信、支付宝订单数据
						success: function(res) {
							if (res.errMsg === 'requestPayment:ok') {
								uni.showToast({
									title: '付款成功'
								});
							}
							uni.redirectTo({
								url:'./payresult'
							})
						},
						fail: function(err) {
							console.log(JSON.stringify(err.errMsg));
						}
					});
				},
				fail: (e) => {
					this.GLOBAL.requestFail(e);
				}
			});
		},
		alipay() {
			uni.request({
				url: this.GLOBAL.serverSrc + 'pay/alipay/payorder',
				method: 'POST',
				data: {
					order_id: this.orderId,
					remark: this.remark
				},
				success: res => {
					var alipaySdk = res.data.alipay_sdk;
					uni.requestPayment({
						provider: 'alipay',
						orderInfo: alipaySdk, //微信、支付宝订单数据
						success: function(res) {
							if (res.errMsg === 'requestPayment:ok') {
								uni.showToast({
									title: '付款成功'
								});
							}
						},
						fail: function(err) {
							console.log(JSON.stringify(err.errMsg));
						}
					});
				},
				fail: (e) => {
					this.GLOBAL.requestFail(e);
				}
			});
		}
	}
}
